<template>
    <v-app class="blog__app">
        <v-flex>
            <main class="blog" :class="{ 'blog--reading': this.post }">
                <blog-nav :content="content" :filters="filters" :navs="navs"/>
                <blog-feed :filters="filters"/>
                <blog-post :post="post"/>
            </main>
        </v-flex>
        <v-footer class="blog__footer" height="auto" app>
            <div class="blog__footer--center-div">
                <div>
                    <v-btn class="blog__footer--buttons" v-for="icon in icons" :key="icon" icon :href="icon.href">
                        <v-icon size="18px">{{ icon.class }}</v-icon>
                    </v-btn>
                </div>
                <div>
                    <span>
                        &copy; {{ new Date().getFullYear() }} — <strong>Kurozero</strong> | Made with <a href="https://vuejs.org/" target="_blank">vuejs</a>.
                    </span>
                </div>
            </div>
        </v-footer>
    </v-app>
</template>

<script>
    import BlogNav from './BlogNav'
    import BlogFeed from './BlogFeed'
    import BlogPost from './BlogPost'

    export default {
        name: 'blog',
        components: { BlogNav, BlogFeed, BlogPost },
        resource: 'Blog',
        props: {
            post: String,
            author: String
        },
        data() {
            return {
                navs: 0,
                title: '',
                labels: {
                    post: '',
                    author: ''
                },
                icons: [
                    { class: 'fab fa-facebook', href: 'https://www.facebook.com/pepijnvdbroek' },
                    { class: 'fab fa-twitter', href: 'https://twitter.com/kurozer0' },
                    { class: 'fab fa-github', href: 'https://github.com/kurozeropb' },
                    { class: 'fab fa-keybase', href: 'https://keybase.io/kurozero' },
                    { class: 'fab fa-discord', href: 'https://discord.gg/Vf4ne5b' }
                ]
            }
        },
        computed: {
            content() {
                return { title: this.title, labels: this.labels }
            },
            filters() {
                let filters = {};

                if (this.post) filters.post = this.post;
                if (this.author) filters.author = this.author;

                return filters
            }
        },
        watch: {
            '$route.name'(to, from) {
                if (to !== from) this.navs++
            }
        },
        beforeMount() {
            this.$getResource('blog')
        }
    }
</script>



