<template>
    <nav class="nav">
        <h1 class="nav__title">
            <router-link to="/">
                <vue-typer :text="content.title" typeDelay="120" pre-erase-delay="10000" class="nav__title--text"></vue-typer>
            </router-link>
        </h1>

        <transition-group tag="menu" name="nav__item" class="nav__menu">
            <li v-for="label in labels" class="nav__item" :key="label" @click="navBack">
                <i class="nav__item--icon"></i>
                <span class="nav__item--label">{{ label }}</span>
            </li>
        </transition-group>
    </nav>
</template>

<script>
    export default {
        name: 'blog-nav',
        props: {
            navs: Number,
            content: Object,
            filters: {
                type: Object,
                default: () => {
                }
            }
        },
        computed: {
            labels() {
                return Object.keys(this.filters)
                .map(filter => this.content.labels[filter]);
            }
        },
        methods: {
            navBack() {
                if (this.navs && !this.filters.author) this.$router.go(-1);
                else this.$router.push('/');
            }
        }
    }
</script>



